body,
html {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

body {
  width: 100%;
  background-color: $background-color;
  font-size: 14px;
  font-family: sans-serif;
  color: $color;
  display: flex;
  margin: 0;
  padding: 0;
  align-items: center;
  justify-content: center;
 
}

#info {
  position: absolute;
  bottom: 0;
  right: 0;
  // padding: $base-unit / 2;
}

#ga {
  position: absolute;
  bottom: 0;
  left: 0;
}
